import React from "react";

function About() {
    return (
        <div className="centruj">
            <div className="container">
                <h2>O nas, o projekcie</h2>
                <p>Jestemy studentami Politechniki lskiej z Wydziau Matematyki Stosowanej, kierunku Informatyka. Niniejsza aplikacja to realizacja pracy inynierskiej bdca narzdziem  sucym do informowania, zarzdzania oraz przekazywania ocen w rodowisku edukacyjnym. Oprogramowanie
umoliwia edukatorom zarzdzanie kursami, wprowadzanie kluczowych elementw,
takich jak etapy, efekty, punkty i sprawdziany, a kursantom ledzenie ich postpw
i wynikw, co zapewnia przejrzysto procesu nauczania. Prowadzcy powinien
zapozna uczestnikw z dziaaniem oprogramowania na zajciach organizacyjnych,
aby zapewni im dostp do wasnych kont, na ktrych dokumentowane bd ich
indywidualne postpy.</p>
            </div>
        </div>
    );
}

export default About;
